@import url("https://fonts.googleapis.com/css2?family=Belanosima:wght@600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Belanosima:wght@600&family=Special+Elite&family=Ysabeau+Infant:ital,wght@0,400;0,500;0,600;0,700;0,800;1,500;1,600;1,700;1,800&display=swap");
:root {
  --color-primary: #6741d9;
  --color-primary-light: #7950f2;
  --color-text: #dee2e6;
  --color-text-dark: #adb5bd;
  --color-background-100: #343a40;
  --color-background-500: #2b3035;
  --color-background-900: #212529;
  --color-red: #fa5252;
  --color-red-dark: #e03131;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}
/* width */
::-webkit-scrollbar {
  width: 0;
  height: 0;
}

/* Handle */
/* ::-webkit-scrollbar-thumb {
  background: var(--color-primary);
  border-radius: 10px;
} */

body {
  /* font-family: "Belanosima", sans-serif;
  font-family: "Special Elite", cursive; */
  font-family: "Ysabeau Infant", sans-serif;
  color: var(--color-text);
  background: url(../public/images/homepage.jpg) no-repeat;
  padding: 2.4rem;
  position: relative;
  background-size: cover;
  background-position: center;
}
body::after {
  content: "";
  position: absolute;
  background-color: #000000ad;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
/* ****mobile style ***** */
.mobile {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}
.mobile .logo {
  margin-bottom: 2rem;
  text-shadow: 0px 0px 20px;
}
.mobile h1 {
  margin-bottom: 2rem;
  color: yellow;
}
.mobile p {
  font-size: 2rem;
  font-weight: bold;
}
/* ******* */

.nav-bar {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  height: 7.2rem;
  padding: 0 3.2rem;
  border-radius: 0.9rem;
  background-color: #000000a6;
}

.main {
  margin-top: 2.4rem;
  height: calc(100vh - 7.2rem - 3 * 2.4rem);
  display: flex;
  gap: 2.4rem;
  justify-content: center;
}

.box {
  width: 45rem;
  max-width: 45rem;
  border-radius: 0.9rem;
  overflow: scroll;
  position: relative;
  background-color: #00000099;
  /* From https://css.glass */
}

.loader {
  text-align: center;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: 600;
  margin: 4.8rem;
}

.error {
  text-align: center;
  font-size: 2rem;
  padding: 4.8rem;
}

/* ******* */

.logo {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  font-family: "Belanosima", sans-serif;
}

.logo span {
  font-size: 3.2rem;
}

.logo h1 {
  font-size: 4rem;
  font-weight: 700;
  color: red;
}

.search {
  justify-self: center;
  border: none;
  padding: 1.5rem 2rem;
  font-size: 1.8rem;
  border-radius: 0.7rem;
  width: 40rem;
  transition: all 0.3s;
  color: var(--color-text);
  background: none;
  /* border: 1px solid red; */

  border: 0.5px solid #ff0089;

  background-color: #010407;
  border-radius: 3rem;
}

.search::placeholder {
  color: var(--color-text-dark);
}

.search:focus {
  outline: none;
  box-shadow: 0 2.4rem 2.4rem rgba(0, 0, 0, 0.1);
  transform: translateY(-2px);
}

.num-results {
  justify-self: end;
  font-size: 3rem;
  color: #c6003a;
  font-weight: bold;
}

.btn-toggle {
  position: absolute;
  top: 0.8rem;
  right: 0.8rem;
  height: 2.4rem;
  aspect-ratio: 1;
  border-radius: 50%;
  border: none;
  background-color: var(--color-background-900);
  color: var(--color-text);
  font-size: 1.4rem;
  font-weight: bold;
  cursor: pointer;
  z-index: 999;
}

.list {
  list-style: none;
  padding: 0.8rem 0;
  overflow: scroll;
}

.list-watched {
  height: calc(100% - 9rem);
}

.list li {
  position: relative;
  display: grid;
  grid-template-columns: 8rem 1fr;
  grid-template-rows: auto auto;
  column-gap: 2.4rem;
  font-size: 2rem;
  align-items: center;

  padding: 0.5rem 1rem;
  border-bottom: 1px solid var(--color-background-100);
}

.list.list-movies li {
  cursor: pointer;
  transition: all 0.3s;
}

.list.list-movies li:hover {
  background-color: #343a40a8;
}

.list img {
  width: 100%;
  grid-row: 1 / -1;
}

.list h3 {
  font-size: 2rem;
}

.list div {
  display: flex;
  align-items: center;
  gap: 2.4rem;
}

.list p {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

.btn-delete {
  position: absolute;
  right: 2.4rem;
  height: 2.5rem;
  width: 2.5rem;
  border-radius: 50%;
  border: none;

  font-size: 1.5rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s;
  background: none;
}
.btn-delete span {
  width: 100%;
  display: block;
  background: red;
  height: 5px;
  position: absolute;
  transition: 0.5s all;
}
.btn-delete span:first-child {
  transform: rotate(45deg);
}
.btn-delete span:last-child {
  transform: rotate(-45deg);
}
.btn-delete:hover span:first-child {
  transform: rotate(-45deg);
}
.btn-delete:hover span:last-child {
  transform: rotate(45deg);
}
/* ******* */

.summary {
  padding: 2.2rem 3.2rem 1.8rem 3.2rem;
  border-radius: 0.9rem;
  background-color: #57001e75;
  box-shadow: 0 1.2rem 2.4rem rgba(0, 0, 0, 0.2);
}

.summary h2 {
  text-transform: uppercase;
  font-size: 1.6rem;
  margin-bottom: 0.6rem;
}

.summary div {
  display: flex;
  align-items: center;
  gap: 2.4rem;
  font-size: 1.6rem;
  font-weight: 600;
}

.summary p {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

/* ******* */

.details {
  line-height: 1.4;
  font-size: 2rem;
}

.details header {
  display: flex;
}

.details section {
  padding: 4rem;
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
}

.details img {
  width: 33%;
}

.details-overview {
  width: 100%;
  padding: 2.4rem 3rem;
  display: flex;
  flex-direction: column;
  gap: 1.4rem;
}

.details-overview h2 {
  font-size: 2.4rem;
  margin-bottom: 0.4rem;
  line-height: 1.1;
}

.details-overview p {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

.rating {
  background-color: #343a4066;
  border-radius: 0.9rem;
  padding: 2rem 2.4rem;
  margin-bottom: 0.8rem;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
}

.btn-add {
  background-color: #ff000054;
  color: var(--color-text);
  border: none;
  border-radius: 10rem;
  font-size: 1.5rem;
  padding: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s;
}

.btn-add:hover {
  background-color: red;
}

.seconds {
  background-color: var(--color-background-100);
  width: 8rem;
  border-radius: 10rem;
  font-weight: 600;
  text-align: center;
  padding: 0.4rem;
  margin-top: 2.4rem;
}

.btn-back {
  position: absolute;
  top: 0.6rem;
  left: 0.6rem;
  height: 3.2rem;
  aspect-ratio: 1;
  border-radius: 50%;
  border: none;
  /* background-color: var(--color-text); */
  background-color: #fff;
  color: var(--color-background-500);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.8);
  font-family: sans-serif;
  font-size: 2.4rem;
  font-weight: bold;
  cursor: pointer;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
}
.star:hover {
  transform: scale(1.5) rotate(45deg);
}
.big {
  font-size: 5rem;
}
